.wp-block-media-manager-media-player {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0.5em;
  border-radius: 50vh;
}

.wp-media-manager-player-button {
  margin: 0 5px 0 0;
  display: inline-block;
  align-self: center;
  max-width: var(--responsive--aligndefault-width);
  border-radius: 9999px;
  line-height: 0;
}
.wp-media-manager-player-button button {
  padding: 0;
  border: 0;
}
.wp-media-manager-player-button button:focus {
  outline: none;
}
.wp-media-manager-player-button button svg {
  fill: currentColor;
}
.wp-media-manager-player-button button.wp-media-manager-player-button__button:not(:hover):not(:active):not(.has-text-color) {
  background-color: transparent;
  border-color: currentColor;
  color: inherit;
}

.is-small-size button {
  width: 24px;
  min-width: 24px;
  height: 24px;
}

.is-normal-size button {
  width: 36px;
  min-width: 36px;
  height: 36px;
}

.is-medium-size button {
  width: 48px;
  min-width: 48px;
  height: 48px;
}

.is-large-size button {
  width: 60px;
  min-width: 60px;
  height: 60px;
}

.is-giant-size button {
  width: 102px;
  min-width: 102px;
  height: 102px;
}